.scoreboard{
    display:flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-evenly;
    width: 20rem;
    font-size: 1.5rem;
    background-color: white;
    margin: 3rem auto;
    box-shadow: 0px 0px 8px #888;
    border-radius: 0.5rem;
    font-weight: bold;

}

.score{
    width: 100%;
    text-align: center;
    padding: 1rem 0;
}

.xscore{
    color: orangered;
    border-bottom: 5px solid orangered;
    border-radius: 0.5rem 0 0 0.5rem;
}

.oscore{
    color: rgb(13, 161, 206);
    border-bottom: 5px solid rgb(13, 161, 206);
    border-radius: 0 0.5rem 0.5rem 0.5;
}

.inactive{
    border-bottom: 5px solid transparent;
}
